.presentation-text {
  max-width: 355px;
  padding-top: 47px;

  h2 {
    margin-bottom: 9px;
  }

  span {
    display: inline-block;
    vertical-align: baseline;
    font-family: $ff-roboto-condensed;
    font-size: 18px;
    line-height: 27px;
    font-weight: 400;
    color: $color-blue-zodiac;

    margin-bottom: 39px;
  }

  p {
    font-family: $ff-roboto-condensed;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    letter-spacing: -0.2px;
    color: $color-blue-zodiac;

    margin: 0;
  }

  &__description + &__description {
    margin-top: 6px;
  }
}
